	module.exports = {"data":{"post":{"html":"<p>Today my employer announced further cost cutting measures effective January 1, despite the voluntary cuts in vacation and pay we agreed to last month. We are closing the development center in Leipzig, laying off all 22 employees and dropping our mobile finance and HBCI lines that were developed there, and laying off an additional 5 employees in Hamburg.  We'll be down to 110 employees, less than half that of two years ago, when we had offices in Singapore, New York, and Johannesburg. Now we have just Hamburg. My job is safe for now. We'll see in 6 months.</p>","fields":{"title":"Hour of Truth","date":"15 Nov 2002","url":"https://papascott.de/2002-11-15-hour-of-truth/"}}},"pathContext":{"slug":"/2002-11-15-hour-of-truth/","prev":{"fields":{"title":"My Domain Got Farked!","date":"14 Nov 2002","slug":"/2002-11-14-my-domain-got-farked/"}},"next":{"fields":{"title":"Springsteen Kommt","date":"15 Nov 2002","slug":"/2002-11-15-springsteen-kommt/"}}}}

